<template>
    <div>
        <bh-button @click='doImport'>{{options.btnText}}</bh-button>
    </div>
</template>

<script>
    /**
     * 
     * @module BhExcelUpload
     *
     * @example
     * <caption>html</caption>
     * <bh-excel-upload :options='options'></bh-excel-upload>
     * 
     * @example
     * <caption>javascript</caption>
     * export default {
     *   data: () => ({
     *       options: {
     *           btnText: 'Excel',
     *           filetype: ['.xls'], //  ['.xls', '.xlsx']
     *           url: 'http://172.16.7.75:8000/bh-vue/excelimport/upload', // 
     *           beforeSubmit (e, data) { // 
     *               data.formData = {a: 1, b: 2};
     *           },
     *           onFileUploaded (e, data) { // data token()
     *               console.log(data);
     *               return data.result.code === '0';
     *           },
     *           doCheck (data) {
     *               //  resolve 
     *               return new Promise(resolve => {
     *                   setTimeout(() => {
     *                       resolve({isSuccess: true, success: 10, total: 10});
     *                   }, 1000);
     *               });
     *               // 
     *               // return Promise.resolve({isSuccess: false, success: 10, total: 20});
     *           },
     *           doImport (e, opt, downTplData, options) {
     *              //  resolve 
     *              return new Promise(resolve => {
     *                  setTimeout(() => {
     *                      resolve({count: 10});
     *                  }, 1000);
     *              });
     *          }
     *       }
     *   })
     * }
    */
    import BhButton from '../bh-button/bhButton';
    import adapter from './uploadAdapter';

    const ADAPTER_NAME = '__BH_EXCEL_UPLOAD_ADAPTER__'; // emapso ...
    const NOOP = function() {};

    window[ADAPTER_NAME] = window[ADAPTER_NAME] || adapter;

    export default {
        data () {
            return {
                iv: null // import view
            };
        },
        /**
         * @property {Object} options 
         * @property {String} url 
         * @property {String} [btnText=] 
         * @property {String} [filetype=['.xls', '.xlsx']] 
         * @property {Function} [onFileUploaded] * boolean 
         * @property {Function} [beforeSubmit] 
         * @property {Function} doCheck  promise
         * @property {Function} doImport  promise
         * @property {Function} downloadErrorFile 
         * @property {Function} downloadTmplFile 
         */
        props: {
            options: {
                type: Object,
                required: true,
                default () {
                    return {
                        doCheck: NOOP,
                        doImport: NOOP,
                        downloadErrorFile: NOOP,
                        downloadTmplFile: NOOP
                    };
                }
            }
        },
        methods: {
            doImport () {
                let opts = this.options
                if (!opts.filetype) {
                    opts.filetype = ['.xls', '.xlsx']
                }

                if (!opts.btnText) {
                    opts.btnText = ''
                }

                opts = $.extend({adaptor: ADAPTER_NAME}, opts);
                const iv = $.emapImport2(opts);

                this.iv = iv;
            }
        },
        ready () {
        },
        beforeDestroy () {
            let iv = this.iv
            if (!iv) {
                return
            }

            iv.close && iv.close();
            iv.destroy && iv.destroy();
        },
        components: {BhButton}
    };
</script>



